"use client";
import React from "react";
import { useEffect, useState, useRef } from "react";
import Image from "next/image";
import { useRouter } from "next/navigation";
import animationLoad from "./Animation - 1748797716617.json";
const Page = () => {
  const route = useRouter();
  const [data, setData] = useState<{
    publicUserId: string;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    products: any[];
  } | null>(null);
  const [upvoteLoadingId, setUpvoteLoadingId] = useState<string | null>(null);

  useEffect(() => {
    const fetchGuest = async () => {
      const res = await fetch("/api/guest", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ userAgent: navigator.userAgent }),
      });

      const result = await res.json();
      console.log("API result:", result);
      setData(result);
    };

    fetchGuest();
  }, []);
  console.log(data);
  const normalizedStatuses = data?.products?.map((product) => ({
    ...product,
    status: product.status.toLowerCase(),
  }));
  console.log(normalizedStatuses, "statuses");
  const groupedByStatus =
    normalizedStatuses?.reduce((acc, product) => {
      const status = product.status.toLowerCase();
      if (!acc[status]) acc[status] = [];
      acc[status].push(product);
      return acc; // eslint-disable-next-line @typescript-eslint/no-explicit-any
    }, {} as Record<string, any[]>) ?? {};
  console.log(groupedByStatus, "groupedByStatus");
  console.log(groupedByStatus.planned, "groupedByStatus");
  const statuses = ["planned", "in-progress", "live"];
  const statusLabels: Record<
    string,
    { label: string; text: string; color: string }
  > = {
    planned: {
      label: "Planned",
      text: "Ideas prioritized for research",
      color: "#F49F85",
    },
    "in-progress": {
      label: "In-Progress",
      text: "Currently being developed",
      color: "#AD1FEA",
    },
    live: {
      label: "Live",
      text: "Released features",
      color: "#62BCFA",
    },
  };

  return (
    <div className="flex items-start justify-center gap-10 min-h-screen p-10 view">
      <section className="header">
        <div>
          <button onClick={() => route.push("/")}>
            <Image
              src={"/assets/shared/icon-arrow-left.svg"}
              width={8}
              height={4}
              alt="arrow left"
            />
            Go Back
          </button>
          <h3>Roadmap </h3>
        </div>
        <button onClick={() => route.push("/add")}>+ Add Feedback</button>
      </section>
      <section className="roadmapContent">
        {groupedByStatus &&
          statuses.map((status) => (
            <ul key={status}>
              <h3 key={status}>
                {statusLabels[status].label}
                <span>
                  {`(${
                    groupedByStatus[status]
                      ? groupedByStatus[status].length
                      : "0"
                  })`}
                </span>
              </h3>
              <p>{statusLabels[status].text}</p>
              <div>
                {
                  // eslint-disable-next-line @typescript-eslint/no-explicit-any
                  groupedByStatus[status]?.map((product: any) => (
                    <div key={product._id}>
                      <p>
                        <span></span>
                        {statusLabels[status].label}
                      </p>
                      <h4>{product.title}</h4>
                      <p>{product.description}</p>
                      <p>{product.category}</p>
                      <p
                        className="upvotes"
                        onClick={async () => {
                          setUpvoteLoadingId(p._id);
                          const res = await fetch("/api/upvote", {
                            method: "POST",
                            headers: { "Content-Type": "application/json" },
                            body: JSON.stringify({
                              productId: p._id,
                              publicUserId: data?.publicUserId,
                            }),
                          });

                          const result = await res.json();

                          setData((prev) => {
                            if (!prev) return prev;
                            return {
                              ...prev,
                              products: prev.products.map((prod) =>
                                prod._id === p._id
                                  ? { ...prod, upvotes: result.upvotes }
                                  : prod
                              ),
                            };
                          });
                          setUpvoteLoadingId(null);
                        }}
                      >
                        {upvoteLoadingId === p._id ? (
                          <Lottie
                            animationData={animationLoad}
                            loop={true}
                            autoplay={true}
                            className="upvoteLoad"
                          />
                        ) : (
                          <>
                            <Image
                              src={`/assets/shared/icon-arrow-up.svg`}
                              width={8}
                              height={4}
                              alt="comment"
                            />
                            {p.upvotes}
                          </>
                        )}
                      </p>
                    </div>
                  ))
                }
              </div>
            </ul>
          ))}
      </section>
    </div>
  );
};

export default Page;
